main {
    padding: 10px 10%;
    box-sizing: border-box;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

@supports (height: 100vh) {
    body {
        background: linear-gradient(to right top, #60cfff 50%, #DDD 50%);
        background-size: 100% calc(100% - 100vh + 129px);
        background-repeat: no-repeat;

    }

    body:before {
        content: '';
        position: fixed;
        top: 5px;
        bottom: 0;
        width: 100%;
        z-index: -1;
        background: #282c34;
    }
}
